import React, { Component } from 'react';
import Button from 'component/UI/Button';
import { connect } from 'react-redux';
import * as action from 'action';
import { blankDiv } from 'utils';
import './Game.css';
import { withTranslation } from 'react-i18next';
//import $ from 'jquery';

class Game extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
      this.props.setHistory(this.props.history);
    }

    this.state = {
      showPopup: false,
      index: 0,
    }
    
    this.spots = [
      { id: 'spot_00', 
        name: '이효석 문화예술촌',
        img: '//tong.visitkorea.or.kr/cms/resource/35/2365235_image2_1.jpg',
        position: { left: '91px', top: '139px' } },
      { id: 'spot_01', 
        name: '청옥산 육백마지기',
        img: '//tong.visitkorea.or.kr/cms/resource/34/1825134_image2_1.jpg',
        position: { left: '207px', top: '326px' } },
      { id: 'spot_02', 
        name: '월정사',
        img: '//tong.visitkorea.or.kr/cms/resource/94/1840794_image2_1.jpg',
        position: { left: '246px', top: '50px' } },
      { id: 'spot_03', 
        name: '평창 바위공원',
        img: '//tour.pc.go.kr/files/2017/03/15/374003e40abb30eb7095ce657eb6a91c141027.jpg',
        position: { left: '103px', top: '354px' } },
      { id: 'spot_04', 
        name: '올림픽 기념관',
        img: '//i.ytimg.com/vi/rRZZma7LPh4/maxresdefault.jpg',
        position: { left: '340px', top: '90px' } },
      { id: 'spot_05', 
        name: '평창 송어축제',
        img:'//www.festival700.or.kr/upload/board/thumb/607779C69FEBA5.jpg',
        position: { left: '196px', top: '176px' } },
    ];
  }

  startUnity(scene) {
    try {
      switch(window.os) {
        case 'AND':
          if (window.Android) {
            window.Android.startUnity(scene);
          }
          break;
        case 'IOS':
          //console.log('send to ios');
          window.webkit.messageHandlers.buttonClicked.postMessage({ ButtonId: scene });
          break;
        default:
          break;
      }
    }
    catch(e){
      console.log(e);
    }
  }

  onClick(index) {
    this.setState({
      ...this.state,
      showPopup: true,
      index: index,
    })
  }

  renderMain() {
    var scale = window.innerWidth > 402 ? window.innerWidth / 402 : 1;
    var offset = (469 * scale * 0.5) - (469 * 0.5);
    var style = {
      transform: `scale(${scale})`,
      backgroundImage: 'url(/resources/game_map.png)',
      top: offset,
      width: '402px',
      height: '469px',      
    };

    return (
      <div id='dv-game-main' className='transition-default'>
        <div style={style} id='dv-game-map'>
          { this.renderItems() }
        </div>
      </div>
      );
  }

  onHide() {
    this.setState({
      ...this.state,
      showPopup: false,
    })
  }

  renderPopup() {
    const { t } = this.props;
    var data = this.spots[this.state.index];
    return (
      <div id='dv-game-popup'>
        <div className={`popup-back ${this.state.showPopup ? 'show' : 'hidden'}`} onClick={()=>{ this.onHide(); }}>
          <div className='transition-default'/>
        </div>
        <div className={`transition-fast popup-window ${this.state.showPopup ? 'show' : 'hidden'}`}>
          <div className='popup-image'>
            <img src={data.img} />
          </div>
          <div className='popup-body'>
            <div className='title'><b>{t(`about.${data.id}`)}</b></div>
            <div>강원도 평창군 진부면 오대산로 374-8</div>
            <div className='popup-button-group'>
              <div className='popup-button'>{t('ar.info')}</div>
              <div className='popup-button'>{t('ar.directions')}</div>
            </div>
            <div className='popup-button-play'>{t('ar.play')}</div>
          </div>
        </div>        
      </div>
    );
  }

  renderButtonOld(){
    const { t } = this.props;

    return (
      <div>
        <Button text={t('game.artest')} value={"Artest"} 
          onClick={ (obj) => { this.onClick(obj); } }/>
        <Button text={t('game.avo')} value={"Avo"}
          onClick={ (obj) => { this.onClick(obj); } }/>
        <Button text={t('game.mole')} value={"Mole"}
          onClick={ (obj) => { this.onClick(obj); } }/>
        <Button text={"POI TEST"} value={"POI"}
          onClick={ (obj) => { this.onClick(obj); } }/>
        <Button text={t('game.sliding')} value={"Sliding"}
          onClick={ (obj) => { this.onClick(obj); } }/>
      </div>);
  }

  renderItems() {
    var result = [];
    const { t } = this.props;

    this.spots.forEach((element, index) => {
      var name = t(`about.${element.id}`);
      //name = element.name;
      result.push(
        <div className='game-item'           
        onClick={()=>{this.onClick(index)}}
        style={element.position}>
          <img src='/resources/icon.png' className={index % 2 === 0 ? 'gray' : ''}/>
          <div>
            <span>
              {name}
            </span>
          </div>
        </div>);
    });
    return result;
  }

  render(){
    return (
      <div id='dv-game'>
        {blankDiv('50px')}
        <div id='dv-game-button-list' className='show'>
          {this.renderMain()}
          {this.renderPopup()}
        </div>
      { blankDiv('60px') }
      </div>);
  }
}

const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(withTranslation()(Game));